body{
    background: rgb(0,0,255);
    background: linear-gradient(90deg, rgba(0,0,255,0.7931547619047619) 0%, rgba(0,212,255,1) 61%);
} 
form {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
}
.input-todo {
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-right: 10px;
}
button {
  padding: 10px 20px;
  font-size: 16px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
button:hover {
  background-color: #0056b3;
}
.TodoListWrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.TodoList {
  background-color: #f5f5f5;
  padding: 10px;
  margin-bottom: 5px;
  border-radius: 5px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.TodoList .todo-title {
  flex: 1;
  margin-right: 10px;
}
.TodoList .delete-button {
  background-color: #e74c3c;
  color: #fff;
  padding: 5px 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
.TodoList .delete-button:hover {
  background-color: #c0392b;
}
li {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #f5f5f5;
  padding: 10px;
  margin-bottom: 5px;
  border-radius: 5px;

  &:hover {
    background-color: #e6e6e6;
  }
}
span {
  flex: 1;
  margin-right: 10px;
}

input {
  flex: 1;
  margin-right: 10px;
  padding: 5px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

button {
  padding: 5px 10px;
  font-size: 14px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-left: 5px;

  &:hover {
    background-color: #0056b3;
  }
}
.delete-button {
  background-color: #e74c3c;
  
  &:hover {
    background-color: #c0392b;
  }
}
  